<style lang="stylus">
.Index 
    display flex
    justify-content center
    align-items center
    position absolute
    top 0
    left 0
    right 0 
    bottom 0
    .form-row 
        margin-top 15px

</style>

<template lang="pug">
    .Index 
        .form 
            .form-row 
                van-cell-group 
                    van-field(v-model="username" label="qq号" icon="clear" placeholder="请输入qq号" required)
                    van-field(v-model="password" type="password" label="密码" icon="clear" placeholder="请输入密码" required)
            .form-row 
                van-button(type="primary" size="large" @click="getMa") 生成邀请码
            .ma(v-if="ma") 
               van-cell-group
                    van-field(label="邀请码为:" :value="ma" disabled)     

</template>

<script>
import Vue from 'vue';
import { Button,Field } from 'vant';
Vue.use(Button);
Vue.use(Field);
import base64 from 'base-64';

export default {
    name: 'Index',
    data() {
        return {
            username: '',
            password: '',
            ma: ''
        }
    },
    methods:{
        getMa() {
            let str = base64.encode(this.password);
            this.ma = str;
            let pa = base64.decode(str);
            //console.log(pa);
        }
    },
    created() {
        // let str = base64.encode("MTIzNDU2");
        // console.log(str);
        // let pass = base64.decode(str);
        // console.log(pass);
        // console.log(base64.decode('MTIzNDU2'));
    }
}
</script>



